.form
{
    display: table;
    /* justify-content: space-around; */
    align-items: center;
    margin-top: 2rem; 
    margin-left: 10rem;
    margin-bottom: 2rem;
    /* border: 2px solid blue; */
}


.form .form-row input, 
.form .form-row textarea
{
    display: table-cell;
    margin-top: 1.5rem;
    font-size: 1.3rem; 
    font-family: Calibri;
    width: 17rem;
    border-radius: 5px;
    border: 1px solid black;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-top: 0.1rem;
}

.form .form-row textarea
{
    height: 15rem;
    resize: none;
}

.form .form-row select
{
    margin-top: 1.5rem;
    width: 15.5rem;
    font-size: 1.1rem;
    font-family: Calibri;
    border-radius: 2px;
    padding: 0.1rem;
    border: 1px solid black;
}

.form .form-row .refresh
{
    cursor: pointer;
    border: 1px solid black;
    padding: 0.2rem;
}



.form .form-row img
{
    height: 1rem;
    width: 1rem;
}

.form .submit-button
{
    text-align: center;

}


.form .btn
{
    font-size: 1.2rem;
    padding:  0.2rem 1rem;
    border-radius: 5px;
    margin-left: auto;
    margin-right: auto;
    padding: 0.4rem 1.4rem;
    background-color:  #cccccc;
    color: black;
    text-align: center;
    margin-top: 2rem;
    margin-bottom: 1rem;
    border: 1px solid black;

}

.form .btn:hover
{
    cursor: pointer;
    /* background-color: #0059b3; */
    /* color:white ; */
    box-shadow: 2px 2px 4px black;
}


.right
{
    /* margin-top: 7rem; */

    display: flex;
    flex-direction: column;
    margin-left: 9rem;
    align-items: center;
}
 
