.coded 
{
    height: 100vh;
    width: 98.7vw;
    
    background-color: black;
    /* border: 10px solid red; */
    /* margin-top: 100vh; */
    /* padding-top: 30vh; */
    overflow: hidden;

    display: flex;
    justify-content: space-around;
    align-items: center;
}


.coded .text
{
    color: white;
    font-family: 'Metal Mania', cursive;
    font-size: 3rem;
}

.email, .github
{
    font-family: 'Dancing Script', cursive;
    font-size: 1.5rem;
    display: flex;
    margin-top: 1rem;
}

.email img,
.github img
{
    height: 40px;
    width: 40px;
    /* border: 3px solid red; */
    border: 1px solid black;
    margin-right: 1rem;

}

.github img
{
    background-color: white;
    border-radius: 50%;

}

.personAbout
{
    height:320px;
    width: 300px;
    position: relative;
    background: #35424a;
    
    box-sizing: border-box;
    box-shadow: 5px 5px 5px #35424a;
    border-radius: 50%;
}

.personAbout .imgBoxAbout
{
    position: relative;
    
    overflow: hidden;
    border-radius: 50%;
    
}

.personAbout .imgBoxAbout img
{
    height:320px;
    width: 300px;
    transition: transform 2s;
    border-radius: 50%;
}

.personAbout:hover .imgBoxAbout img
{
    transform: scale(1.2);
    
}

.personAbout .nameAbout
{
    height:300px;
    width: 280px;
    position: absolute;
    
    top: 10px;
    left: 10px;
    right: 10px;
    bottom: 10px;
    background: rgba(0,0,0,.8);
    color: #ffffff;
    transform: scaleY(0);
    transition: transform .5s;
    border-radius: 50%;
}
.personAbout .nameAbout p
{
    font-size: 50px;
    font-family: 'Dancing Script', cursive;
    font-weight: 10;
    padding-top: 15vh;
    padding-left: 2vw;
    text-align: center;
}

.personAbout:hover .nameAbout
{
    transform: scaleY(1);
}

